export default meta;
type Story = StoryObj<typeof Stepper>;

// Template for interactive stories
const Template = (args: any) => {
  const [currentStep, setCurrentStep] = useState(args.currentStep || 1);

  return (
    <div style={{minHeight: '120px', width: '100%'}}>
      <Stepper
        {...args}
        currentStep={currentStep}
        onStepClick={(stepNumber, step, index) => {
          setCurrentStep(stepNumber);
          action('onStepClick')(stepNumber, step, index);
        }}
      />
    </div>
  );
};

